"use client";

import React, { useState } from "react";
import { useSession, signIn, signOut } from "next-auth/react";
import JobList from "../components/JobList";

const jobSites = [
  "Dynamite Jobs",
  "Hubstaff Talent",
  "Outsourcely",
  "Jobspresso",
  "Working Nomads",
  "Virtual Vocations",
  "Skip The Drive",
  "Remote Woman",
  "The Muse",
  "Remote OK",
  "Toptal",
  "Upwork",
  "Wellfound",
  "JustRemote",
  "We Work Remotely",
  "FlexJobs",
  "Indeed",
  "Dice",
  "Monster",
  "LinkedIn",
  "Career Builder",
  "ZipRecruiter",
];

export default function Home() {
  const { data: session } = useSession();
  const [query, setQuery] = useState("");
  const [selectedSite, setSelectedSite] = useState("All Sites");
  const [jobs, setJobs] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const handleSearch = async () => {
    if (!query.trim()) {
      setError("Please enter a search term.");
      setJobs([]);
      return;
    }
    setError("");
    setLoading(true);
    setJobs([]);
    try {
      const siteParam = selectedSite === "All Sites" ? "" : `&site=${encodeURIComponent(selectedSite)}`;
      const res = await fetch(`/api/search?query=${encodeURIComponent(query)}${siteParam}`);
      if (!res.ok) {
        throw new Error("Failed to fetch jobs");
      }
      const data = await res.json();
      setJobs(data.jobs || []);
    } catch (err: any) {
      setError(err.message || "An error occurred");
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="min-h-screen bg-white text-black flex flex-col items-center p-6 font-sans">
      <header className="w-full max-w-3xl flex justify-between items-center mb-8">
        <h1 className="text-5xl font-extrabold tracking-tight">Job Search Aggregator</h1>
        <div>
          {session ? (
            <div className="flex items-center space-x-4">
              <p className="text-gray-700">Signed in as {session.user?.email}</p>
              <button
                onClick={() => signOut()}
                className="bg-gray-200 text-black px-4 py-2 rounded hover:bg-gray-300 transition"
              >
                Sign out
              </button>
            </div>
          ) : (
            <button
              onClick={() => signIn("google")}
              className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700 transition"
            >
              Sign in with Google
            </button>
          )}
        </div>
      </header>
      <div className="w-full max-w-3xl flex gap-4 mb-8">
        <input
          type="text"
          placeholder="Search jobs..."
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          className="flex-grow border border-gray-300 rounded-lg px-6 py-3 text-black placeholder-gray-500 focus:outline-none focus:ring-4 focus:ring-black transition"
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              handleSearch();
            }
          }}
          aria-label="Job search input"
        />
        <select
          value={selectedSite}
          onChange={(e) => setSelectedSite(e.target.value)}
          className="border border-gray-300 rounded-lg px-4 py-3 text-black focus:outline-none focus:ring-4 focus:ring-black transition"
          aria-label="Select job site"
        >
          <option>All Sites</option>
          {jobSites.map((site) => (
            <option key={site} value={site}>
              {site}
            </option>
          ))}
        </select>
        <button
          onClick={handleSearch}
          className="bg-black text-white px-8 py-3 rounded-lg hover:bg-gray-900 transition font-semibold"
          aria-label="Search jobs"
        >
          Search
        </button>
      </div>
      {error && <p className="text-red-600 mb-6">{error}</p>}
      {loading ? (
        <p className="text-lg font-medium">Loading jobs...</p>
      ) : (
        <JobList jobs={jobs} />
      )}
    </main>
  );
}
