"use client";

import React from "react";

interface Job {
  id: string;
  title: string;
  company: string;
  location?: string;
  url: string;
  source: string;
  snippet?: string;
}

interface JobListProps {
  jobs: Job[];
}

const JobList: React.FC<JobListProps> = ({ jobs }) => {
  if (jobs.length === 0) {
    return <p className="text-center text-gray-500 mt-8">No jobs found.</p>;
  }

  return (
    <div className="w-full max-w-3xl space-y-6">
      {jobs.map((job) => (
        <a
          key={job.id}
          href={job.url}
          target="_blank"
          rel="noopener noreferrer"
          className="block border border-gray-300 rounded-lg p-6 hover:shadow-lg transition bg-white"
        >
          <h2 className="text-2xl font-semibold mb-1">{job.title}</h2>
          <p className="text-gray-700 text-lg">{job.company}</p>
          {job.location && <p className="text-gray-500">{job.location}</p>}
          {job.snippet && <p className="mt-3 text-gray-600 text-base">{job.snippet}</p>}
          <p className="mt-4 text-xs text-gray-400">Source: {job.source}</p>
        </a>
      ))}
    </div>
  );
};

export default JobList;
